import React from 'react';
import s from './Footer.module.css'


const Footer = () => {
    return (

        <footer className={s.footer}>

            <div className={s.soc}><div>We are in social networks</div>
            <div className={s.icon}>
                    <a href=""><img src="https://image.flaticon.com/icons/png/128/1051/1051360.png" alt="" /></a>
                    <a href=""><img src="https://image.flaticon.com/icons/png/128/1077/1077042.png" alt="" /></a>
                    <a href=""><img src="https://image.flaticon.com/icons/png/128/2111/2111813.png" alt="" /></a>
                </div>

            </div>
            <div className={s.footerInfo}>
                <img src='https://bridgewatercentre.ie/app/uploads/2018/11/GADGET-LAND-STORE-LOGO.png' />
            </div>

        </footer>
    )
}

export default Footer;