import React from 'react';
import s from './Categorypart.module.css'
import { NavLink } from 'react-router-dom';


const Categorypart = (props) => {
    return (

        <div className={s.category}>
                <span>Category</span>
                <input type="text" size="60" />
                <button>Search</button>
            <div className={s.korzina}>
                <NavLink to="/korzina">
                    <img src="https://img2.freepng.ru/20180413/ehe/kisspng-shopping-cart-logo-clip-art-shoping-5ad149c191d585.0560727015236653455974.jpg" alt="" />
                    <div className={s.nameKorzina}>Basket</div>
                </NavLink>
                
            </div>
        </div>




    )
}

export default Categorypart;