import React from 'react';
import s from './Navbar.module.css';
import { NavLink } from 'react-router-dom';
import FontAwesome from 'react-fontawesome';


const Navbar = () => {
  return (
    <div>
      <nav className={s.nav}>
        <div className={s.item}><NavLink to="/Phones">
          <div className={s.fonts}>
            <FontAwesome
              className="fas fa-mobile"
               />
          </div>
        Phones</NavLink></div>
        <div className={s.item}><NavLink to="/Computers">
        <div className={s.fonts}>
            <FontAwesome
              className="fas fa-laptop"
               />
          </div>
          Notebooks</NavLink></div>
        <div className={s.item}><NavLink to="/Gadgets">
        <div className={s.fonts}>
            <FontAwesome
              className="fas fa-tablet"
               />
          </div>
          Gadgets</NavLink></div>
        <div className={s.item}><NavLink to="/Bags">
        <div className={s.fonts}>
            <FontAwesome
              className="fas fa-headphones"
               />
          </div>
          Headphones</NavLink></div>
        <div className={s.item}><NavLink to="/Sports">
        <div className={s.fonts}>
            <FontAwesome
              className="fas fa-camera"
               />
          </div>
          Video camera</NavLink></div>
        <div className={s.item}><NavLink to="/Beauty">
        <div className={s.fonts}>
            <FontAwesome
              className="fas fa-camera"
              />
          </div>
          Accessory</NavLink></div>

      </nav>
    </div>
  )
}

export default Navbar;